@import "../../../../shared/styles/styles.scss";
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  display: flex;

  align-items: center;

  width: 100%;
  min-height: 64px;

  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;

  color: #fff;
  background-color: $primary-bg-color;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
